import xflSelect from '@/components/xfl-select/xfl-select.vue';
import {
	login,
	getList
} from '@/api/api.js'
import {validLogin} from '@/api/valid.js'
export default {
	data() {
		return {
			platform: '',//当前设备环境
			currentH: 0,
			username: '',
			password: '',
			msg: '',
			list: [
				// '苹果',
				// {value: '香蕉', disabled: true},
				// '葡萄',
				// '芒果',
				// '大白菜',
			],
			selectIndex:0
			//requestStr: '[{"USERID":"admin","MACIP":""},{"emplid":"0638721","password":"123456"}]'
		}
	},
	onLoad(e) {
		console.log(uni.getStorageSync("updateDialogFlag"));
		if (!uni.getStorageSync("updateDialogFlag")) {
			uni.setStorageSync("updateDialogFlag", "true");
			//this.updateMsg();
		}
		this.platform = uni.getSystemInfoSync().platform;
		console.log(uni.getSystemInfoSync().windowHeight);
		this.currentH = uni.getSystemInfoSync().windowHeight;
		this.getList();
	},
	components: {
		xflSelect
	},
	onPullDownRefresh() {
		let updateDialogFlag = uni.getStorageSync("updateDialogFlag");
		console.log('onPullDownRefresh', this.updateDialogFlag);
		if (uni.getStorageSync("updateDialogFlag") == "true") console.log(1);uni.setStorageSync("updateDialogFlag", "false");//this.updateMsg();
		uni.stopPullDownRefresh();
	},
	methods: {
		getList(){
			getList().then(res=>{
				for(var item in res){
					this.list.push(res[item]);
				}
			})
		},
		selectChange(data){
			console.log(data);
			this.selectIndex=data.index
		},
		updateMsg() {
			const dcRichAlert = uni.requireNativePlugin('DCloud-RichAlert');
			console.log(222);
			dcRichAlert.show({
				position: 'center',
				title: "更新日志",
				titleColor: '#FF0000',
				content: "##\n\n\n" +
					"\t\t\t\t\t如有问题，请263联系信息部-曾磊",
				contentAlign: 'left',
				buttons: [{
					title: '确认',
					titleColor: '#3F51B5'
				}]
			}, result => {
				console.log(result);
				switch (result.type) {
					case 'button':
						uni.setStorageSync("updateDialogFlag", "false");
						console.log("callback---button--" + result.index);
						break;
					case 'checkBox':
						console.log("callback---checkBox--" + result.isSelected);
						break;
					case 'a':
						console.log("callback---a--" + JSON.stringify(result));
						break;
					case 'backCancel':
						console.log("callback---backCancel--");
						break;
				}
			});
		},
		clearInput: function(event) {
			this.inputClearValue = event.target.value;
			if (event.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function() {
			this.inputClearValue = '';
			this.showClearIcon = false;
		},
		changePassword: function() {
			this.showPassword = !this.showPassword;
		},
		getWorkId(event) {
			this.workId = event.target.value;
		},
		getPwd: function(event) {
			this.pwd = event.target.value;
		},
		getLogin: function(workId, pwd) {
			uni.setStorageSync("username", workId);
			console.log('https://oa.fpc98.com/SerFile/Service.asmx/h_in');
			return new Promise((resolve, reject) => {
				uni.request({
					//url:'http://10.10.10.8:85/SerFile/Service.asmx',
					url: 'https://oa.fpc98.com/SerFile/Service.asmx/h_in',
					data: {
						emplid: workId,
						addrter: pwd
					},
					success(res) {
						console.log(res.data);
						var data = res.data.split('<?xml version="1.0" encoding="utf-8"?>');
						var data = data[1].split('<boolean xmlns="http://oa.fpc98.com">');
						var data = data[1].split('</boolean>');
						var data = data[0];
						if (data == "true") {
							resolve(data);
						} else {
							var msg = "帐号或密码不正确"
							resolve(msg);
						}
						// var parser = new DOMParser();//不支持app
						// var xmlDoc = parser.parseFromString(res.data, "text/xml");
						// console.log(xmlDoc.getElementsByTagName("boolean"));
						// var bool = xmlDoc.getElementsByTagName("boolean");
						// console.log(bool[0].innerHTML);
						// if (bool[0].innerHTML == "true") {
						// 	uni.navigateTo({
						// 		url: '/pages/main/main'
						// 	})
						// } else {
						// 	console.log("工号或密码错误！");
						// }

					},
					fail(err) {
						console.log(err);
					}
				})
			})
		},
		login() {
			//登录验证
			var userData={"username":this.username,"password":this.password};
			var valid=validLogin(userData);
			if(!valid.flag){
				uni.showToast({
					title:valid.msg,
					icon:"none"
				})
				return
			}
			login(userData).then(res=>{
				if(res.flag){
					uni.setStorageSync("emplid",this.username)
					uni.showToast({
						title:res.msg
					})
					uni.navigateTo({
						url: '/pages/index/menu'
					})
				}else{
					uni.showToast({
						title:res.msg,
						icon:"none"
					})
				}
			})
			// this.getLogin(this.workId, this.pwd).then(res => {
			// 	if (res == "true") {
			// 		uni.navigateTo({
			// 			url: '/pages/main/main'
			// 		})
			// 	} else {
			// 		this.msg = res;
			// 	}
			// })
		}
	},
}
